/* === Base Navbar Styles === */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #120707;
  color: #fff;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  width: 100%; 
  box-sizing: border-box;
}

.logo {
  font-size: 1.6rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1.8rem;
  margin: 0;
  padding: 0;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #00bcd4;
}

/* === Hamburger Menu (hidden on desktop) === */
.menu-toggle {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  color: #fff;
}

/* === Responsive Styles for Mobile === */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  
  .nav-links {
    position: absolute;
    top: 70px; 
    left: 0; 
    width: 100%; 
    background-color: #0d0d0d;
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem 2rem;
    gap: 1rem;
    display: none; 
    box-sizing: border-box;
  }


  .nav-links.active {
    display: flex;
  }

 
  .nav-links a {
    width: 100%;
    padding: 0.5rem 0;
  }
}
