.about {
  background-color: #131010e0;
  padding: 5rem 2rem;

  /* Flex centering */
  display: flex;
  flex-direction: column;
  justify-content: center; /* vertical centering */
  align-items: center;     /* horizontal centering */

  min-height: 100vh;       /* optional: fill the viewport height */
  text-align: center;      /* center text */
}

.about h2 {
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 1rem;
}

.about p {
  max-width: 700px;
  margin: 0 auto;
  font-size: 1.1rem;
  color: #ffffff;
  line-height: 1.6;
}
