.projects {
  padding: 4rem 2rem;
  background-color: #000000cb;
  color: #fff;
  text-align: center;
}

.projects h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-transform: uppercase; /* Uppercase heading */
  letter-spacing: 2px;
  color: #c2c2c2;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.project-card {
  background-color: #1c1c1c;
  padding: 2rem;
  border-radius: 10px;
  width: 280px;
  box-shadow: 0 0 10px rgba(0, 188, 212, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.6);
}

.project-card h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-transform: uppercase; /* Uppercase project titles */
  color: #98c6cc;
}

.project-card p {
  font-size: 1rem;
  color: #ccc;
  line-height: 1.6;
}
